import React from "react";
import { Row, Col, Card, Button, Carousel } from "antd";
import { Link } from "react-router-dom";
import venom from "../../../assets/img/png/venom.jpeg";
import megalodon from "../../../assets/img/png/megalodon.png";
import shangchi from "../../../assets/img/png/shang-chi.jpg";
import bloodshot from "../../../assets/img/png/bloodshot.jpg";
import piratas from "../../../assets/img/png/piratas.jpg";
import endgame from "../../../assets/img/png/endgame.jpg";
import momia from "../../../assets/img/png/momia.jpg";
import skyline from "../../../assets/img/png/skyline.jpg";
import infinitywar from "../../../assets/img/png/InfinityWar.png";
import spiderman from "../../../assets/img/png/spiderman.jpg";
import ultron from "../../../assets/img/png/ultron.png";
import transformers from "../../../assets/img/png/transformers.jpg";
import venomp from "../../../assets/img/jpg/venomp.jpg";
import momiap from "../../../assets/img/jpg/momiap.jpg";
import megalodonp from "../../../assets/img/jpg/megalodonp.jpg";
import endgamep from "../../../assets/img/jpg/endgamep.jpg";
import bloodshotp from "../../../assets/img/jpg/bloodshotp.jpg";
import infinitywarp from "../../../assets/img/jpg/infinitywarp.jpg";
import piratasp from "../../../assets/img/jpg/piratasp.jpeg";
import transformersp from "../../../assets/img/jpg/transformersp.jpg";
import ultronp from "../../../assets/img/jpg/ultronp.jpg";
import skylinep from "../../../assets/img/jpg/skylinep.jpg";
import spidermanp from "../../../assets/img/jpg/spidermanp.jpg";
import shangchip from "../../../assets/img/jpg/shang-chip.jpg";

import "./Populars.scss";

export default function Populars() {
  return (
    <div className="popular-films">
      <h1> POPULARES</h1>
      <Carousel autoplay>
        <div>
          <Row className="popular-films">
            <Col lg={3}></Col>
            <Col lg={18}>
              <Row className="row-films">
                <Col md={6}>
                  <CardFilms
                    image={bloodshot}
                    title="BloodShot"
                    subtitle="Acción/Aventura"
                    link={bloodshotp}
                    description="Murray Ray Garrison es resucitado por un equipo de científicos. Mejorado con nanotecnología, se convierte en una máquina de matar biotecnológica sobrehumana. Cuando Ray entrena por primera vez con otros super soldados, no recuerda nada de su vida anterior."
                    url="https://www.youtube.com/watch?v=zOeEPSR2OSM"
                    info="https://www.filmaffinity.com/es/film858910.html"
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={shangchi}
                    title="Shang-Chi y la leyenda de los diez anillos"
                    subtitle="Acción/Fantasía"
                    description="El maestro de artes marciales Shang-Chi se enfrenta al pasado que creía haber dejado atrás cuando se ve envuelto en la red de la misteriosa organización de los Diez Anillos."
                    link={shangchip}
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={megalodon}
                    title="Megalodon"
                    subtitle="Acción/Ciencia ficción"
                    link={megalodonp}
                    description="Un tiburón prehistórico de 25 metros de longitud amenaza a los tripulantes de un submarino hundido en la fosa más profunda del océano Pacífico y al grupo enviado para rescatarlos. Si no lo detienen, el tiburón causará grandes estragos."
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={venom}
                    title="Venom"
                    subtitle="Acción/Ciencia ficción"
                    link={venomp}
                    description="Después de encontrar un cuerpo anfitrión en el periodista de investigación Eddie Brock, el simbionte alienígena debe enfrentarse a un nuevo enemigo, Carnage, el alter ego del asesino en serie Cletus Kasady."
                  />
                </Col>
              </Row>
            </Col>
            <Col lg={3}></Col>
          </Row>
        </div>
        <div>
          <Row className="popular-films">
            <Col lg={3}></Col>
            <Col lg={18}>
              <Row className="row-films">
                <Col md={6}>
                  <CardFilms
                    image={piratas}
                    title="Piratas del Caribe. La venganza de Salazar"
                    subtitle="Fantasía/Aventura"
                    link={piratasp}
                    description="El capitán Salazar comanda un ejército de piratas fantasma con el que pretende exterminar a todos los piratas vivos de la Tierra, pero Jack Sparrow se opone a él y busca el Tridente de Poseidón, un objeto mágico que permite a quien lo posee controlar los mares, la única esperanza para detener a Salazar."
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={transformers}
                    title="Transformers: el último caballero"
                    subtitle="Acción/Ciencia ficción"
                    link={transformersp}
                    description="La guerra entre los Transformers y los humanos amenaza con destruir la Tierra. Con el gigante Optimus Prime muy lejos, realizando una misión espacial, el futuro del planeta depende ahora de la extraña alianza forjada entre el inventor Cade Yeager, Bumblebee, un lord inglés y un profesor de Oxford."
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={spiderman}
                    title="Spider-Man: de regreso a casa"
                    subtitle="Acción/Aventura"
                    link={spidermanp}
                    description="Peter Parker asume su nueva identidad como Spider-Man y regresa a vivir con su tía después de su aventura con los Vengadores. Al volver, mientras sigue bajo la tutela de Tony Stark, descubre que ha surgido un nuevo y despiadado enemigo que pretende destruir todo lo que ama: el Buitre."
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={ultron}
                    title="Vengadores: La era de Ultrón"
                    subtitle="Acción/Aventura"
                    link={ultronp}
                    description="Tony Stark quiere lanzar un nuevo programa de paz, pero algo sale mal y acaba creando a Ultrón, un robot que quiere destruir a la humanidad. Thor, Hulk y el resto de los Vengadores deberán juntar sus fuerzas una vez más para luchar contra el robot."
                  />
                </Col>
              </Row>
            </Col>
            <Col lg={3}></Col>
          </Row>
        </div>
        <div>
          <Row className="popular-films">
            <Col lg={3}></Col>
            <Col lg={18}>
              <Row className="row-films">
                <Col md={6}>
                  <CardFilms
                    image={endgame}
                    title="Vengadores: EndGame"
                    subtitle="Acción/Ciencia ficción"
                    link={endgamep}
                    description="Después de los eventos devastadores de Avengers: Infinity War, el universo está en ruinas debido a las acciones de Thanos, el Titán Loco. Con la ayuda de los aliados que quedaron, los Vengadores deberán reunirse una vez más para intentar detenerlo y restaurar el orden en el universo de una vez por todas."
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={momia}
                    title="La momia"
                    subtitle="Acción/Terror"
                    link={momiap}
                    description="Ahmanet, una antigua princesa egipcia maldita, despierta en su cripta y, furiosa y malvada, siembra el terror entre los humanos. La única persona que puede detenerla y evitar que arrase Londres es un intrépido mercenario."
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={infinitywar}
                    title="Vengadores: Infinity War"
                    subtitle="Acción/Ciencia ficción"
                    link={infinitywarp}
                    description="Los superhéroes se alían para vencer al poderoso Thanos, el peor enemigo al que se han enfrentado. Si Thanos logra reunir las seis gemas del infinito: poder, tiempo, alma, realidad, mente y espacio, nadie podrá detenerlo."
                  />
                </Col>
                <Col md={6}>
                  <CardFilms
                    image={skyline}
                    title="Sky Line"
                    subtitle="Ciencia ficción/Suspenso"
                    link={skylinep}
                    description="Un grupo de amigos se despierta debido a una luz misteriosa. La luz dibuja ligeramente la imagen de personas que luego desaparecen de repente. Así descubrirán que una fuerza de otro mundo se está tragando a toda la población de la Tierra."
                  />
                </Col>
              </Row>
            </Col>
            <Col lg={3}></Col>
          </Row>
        </div>
      </Carousel>
    </div>
  );
}

function CardFilms(props) {
  const { title, subtitle, image } = props;
  const { Meta } = Card;

  return (
    <Link to={{ pathname: "/app/pelicula", data: props }}>
      <Card
        className="popular-films__card"
        cover={<img src={image} alt="title" />}
      >
        <Meta title={title} description={subtitle}></Meta>
      </Card>
    </Link>
  );
}
