import React from "react";
import { Row, Col, Button } from "antd";
import "./Film.scss";
import { PlayCircleOutlined, InfoCircleOutlined } from "@ant-design/icons";
import Comments from "../Comments/Comments";
import { Link } from "react-router-dom";

export default function Film(props) {
  const { link, subtitle, title, description, info } = props.location.data;
  const fondo = props.location.fondo;

  return (
    <div className="film">
      <div
        className="film__information"
        style={{ backgroundImage: `url(${link ? link : fondo})` }}
      >
        <Row>
          <Col lg={3} />
          <Col lg={18}>
            <h2>
              {`${title}`} <br />
            </h2>
            <h3>{`${subtitle}`}</h3>
            <h4> {`${description}`}</h4>
            <Row>
              <Link to={{ pathname: "/app/play", data: props }}>
                <Button>
                  <PlayCircleOutlined />
                  Reproducir
                </Button>
              </Link>
              <a href={info} target="_blank" rel="noreferrer">
                <Button>
                  <InfoCircleOutlined />
                  Información
                </Button>
              </a>
            </Row>
          </Col>
          <Col lg={3} />
        </Row>
      </div>
      <div className="film__comments">
        <Row>
          <Col lg={3} />
          <Col lg={18}>
            <Comments title={title} />
          </Col>
          <Col lg={3} />
        </Row>
      </div>
    </div>
  );
}
